"use client";

import { motion } from "motion/react";
import { useEffect, useRef, useState } from "react";
import { ZapFast, ChartBreakoutSquare, AlertCircle } from "@untitledui/icons";
import { FeaturedIcon } from "@/components/foundations/featured-icon/featured-icon";

export const ThreatFacts = () => {
    const ref = useRef<HTMLElement | null>(null);
    const [seen, setSeen] = useState(false);
    const items = [
        {
            icon: ZapFast,
            title: "Attacks are continuous",
            body: "Threat activity runs at all hours — not isolated events.",
        },
        {
            icon: ChartBreakoutSquare,
            title: "Automated and repeated",
            body: "Most threats are scripted and will try again and again.",
        },
        {
            icon: AlertCircle,
            title: "Missed alerts cause damage",
            body: "Risk rises when signals are ignored or lost.",
        },
    ];

    useEffect(() => {
        const el = ref.current;
        if (!el) return;
        const io = new IntersectionObserver(
            (entries) => {
                if (entries[0].isIntersecting) setSeen(true);
            },
            { threshold: 0.25 },
        );
        io.observe(el);
        return () => io.disconnect();
    }, []);

    return (
        <motion.section
            ref={ref}
            initial={{ opacity: 0, y: 12 }}
            animate={seen ? { opacity: 1, y: 0 } : { opacity: 0, y: 12 }}
            transition={{ duration: 0.6, ease: "easeOut", delay: 0.15 }}
            className="relative w-full bg-[#0A0D12] overflow-hidden py-16 md:py-24"
        >
            
            <div className="mx-auto w-full max-w-container px-4 md:px-8">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    animate={seen ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
                    transition={{ duration: 0.6, ease: "easeOut" }}
                    className="mx-auto max-w-4xl text-center"
                >
                    <div className="inline-flex items-center rounded-full p-[1px] ring-1 ring-white/10">
                        <span className="flex items-center gap-2 rounded-full bg-[#0A0D12]/80 px-3 py-1 text-md font-medium text-white/90 ring-1 ring-white/10 backdrop-blur-sm">
                            Modern Threats
                        </span>
                    </div>
                    <h2 className="mt-6 text-3xl font-semibold text-white md:text-4xl">
                        Modern cyber threats are persistent, not occasional
                    </h2>
                    <p className="mx-auto mt-4 max-w-2xl text-lg text-white/75">
                        A factual look at how modern threats operate. Calm, concise, and aligned with real SOC observation.
                    </p>
                </motion.div>

                <motion.div
                    initial={{ opacity: 0, y: 24 }}
                    animate={seen ? { opacity: 1, y: 0 } : { opacity: 0, y: 24 }}
                    transition={{ duration: 0.6, ease: "easeOut", delay: 0.1 }}
                    className="mx-auto mt-12 grid max-w-6xl grid-cols-1 gap-8 text-center md:grid-cols-3"
                >
                    {items.map((item, i) => (
                        <motion.div
                            key={item.title}
                            initial={{ opacity: 0, y: 16 }}
                            animate={seen ? { opacity: 1, y: 0 } : { opacity: 0, y: 16 }}
                            transition={{ duration: 0.6, ease: "easeOut", delay: 0.12 + i * 0.06 }}
                            whileHover={{ y: -4 }}
                            className="flex flex-col items-center rounded-xl bg-[#0B0D12] p-6 ring-1 ring-white/10 shadow-xl md:rounded-2xl md:p-7"
                        >
                            <div className="mb-4">
                                <FeaturedIcon icon={item.icon} size="lg" color="brand" theme="light" />
                            </div>
                            <h3 className="text-xl font-semibold text-white">{item.title}</h3>
                            <p className="mt-2 max-w-xs text-white/70">{item.body}</p>
                        </motion.div>
                    ))}
                </motion.div>
            </div>
        </motion.section>
    );
};
