"use client";

import { motion } from "motion/react";
import { useEffect, useRef, useState } from "react";
import { TagCheckbox } from "@/components/base/tags/base-components/tag-checkbox";

export const ThreatWhySoc = () => {
    const ref = useRef<HTMLElement | null>(null);
    const [seen, setSeen] = useState(false);

    useEffect(() => {
        const el = ref.current;
        if (!el) return;
        const io = new IntersectionObserver(
            (entries) => {
                if (entries[0].isIntersecting) setSeen(true);
            },
            { threshold: 0.25 },
        );
        io.observe(el);
        return () => io.disconnect();
    }, []);

    return (
        <section ref={ref} className="w-full overflow-hidden py-16 md:py-24">
            <div className="mx-auto w-full max-w-container px-4 md:px-8">
                <div className="grid grid-cols-1 gap-10 md:gap-20 lg:grid-cols-2 lg:gap-24">
                    <motion.div
                        initial={{ opacity: 0, x: -16 }}
                        animate={seen ? { opacity: 1, x: 0 } : { opacity: 0, x: -16 }}
                        transition={{ duration: 0.6, ease: "easeOut", delay: 0.1 }}
                        className="relative w-full flex-1 lg:h-128"
                    >
                        <div className="relative w-full rounded-2xl bg-[#0B0D12] ring-1 ring-white/10 shadow-2xl overflow-hidden lg:absolute lg:left-0">
                            <motion.div
                                initial={{ opacity: 0.2 }}
                                animate={{ opacity: 0.35 }}
                                transition={{ duration: 1.2, ease: "easeOut" }}
                                className="absolute inset-0"
                                style={{
                                    backgroundImage:
                                        "linear-gradient(to right, rgba(255,255,255,0.06) 1px, transparent 1px), linear-gradient(to bottom, rgba(255,255,255,0.06) 1px, transparent 1px)",
                                    backgroundSize: "80px 80px, 80px 80px",
                                    backgroundPosition: "center",
                                }}
                            />
                            <motion.div
                                className="absolute left-0 top-0 h-[60%] w-[60%] blur-3xl opacity-30"
                                style={{
                                    background:
                                        "radial-gradient(circle at 30% 30%, rgba(158,119,237,0.28) 0%, rgba(127,86,217,0.14) 35%, rgba(127,86,217,0) 65%)",
                                }}
                                animate={{ x: [0, 12, -8, 0], y: [0, -10, 6, 0] }}
                                transition={{ duration: 18, repeat: Infinity, ease: "linear" }}
                            />
                            <motion.div
                                className="absolute right-0 bottom-0 h-[60%] w-[60%] blur-3xl opacity-25"
                                style={{
                                    background:
                                        "radial-gradient(circle at 70% 70%, rgba(127,86,217,0.24) 0%, rgba(105,65,198,0.12) 35%, rgba(105,65,198,0) 65%)",
                                }}
                                animate={{ x: [0, -14, 10, 0], y: [0, 12, -8, 0] }}
                                transition={{ duration: 22, repeat: Infinity, ease: "linear" }}
                            />
                            <motion.div
                                className="absolute left-1/2 top-1/2 h-[280px] w-[280px] -translate-x-1/2 -translate-y-1/2 rounded-full ring-1 ring-white/15"
                                animate={{ scale: [1, 1.08, 1], opacity: [0.35, 0.6, 0.35] }}
                                transition={{ duration: 3, repeat: Infinity, ease: "easeInOut" }}
                            />
                            <motion.div
                                className="absolute left-1/2 top-1/2 h-[420px] w-[420px] -translate-x-1/2 -translate-y-1/2 rounded-full ring-1 ring-white/10"
                                animate={{ rotate: [0, 8, -8, 0], opacity: [0.15, 0.25, 0.15] }}
                                transition={{ duration: 10, repeat: Infinity, ease: "easeInOut" }}
                            />
                            <div className="absolute inset-0">
                                <motion.span
                                    className="absolute left-[18%] top-[28%] size-2 rounded-full bg-brand-400/90"
                                    animate={{ scale: [1, 1.5, 1], opacity: [0.9, 0.6, 0.9] }}
                                    transition={{ duration: 1.8, repeat: Infinity, ease: "easeInOut" }}
                                />
                                <motion.span
                                    className="absolute left-[72%] top-[62%] size-2 rounded-full bg-brand-400/90"
                                    animate={{ scale: [1, 1.4, 1], opacity: [0.9, 0.6, 0.9] }}
                                    transition={{ duration: 2.1, repeat: Infinity, ease: "easeInOut" }}
                                />
                                <motion.span
                                    className="absolute left-[44%] top-[70%] size-2 rounded-full bg-brand-400/90"
                                    animate={{ scale: [1, 1.6, 1], opacity: [0.9, 0.6, 0.9] }}
                                    transition={{ duration: 1.6, repeat: Infinity, ease: "easeInOut" }}
                                />
                                <motion.div
                                    className="absolute left-[18%] top-[28%] h-px w-[48%] origin-left bg-brand-400/40"
                                    animate={{ opacity: [0.2, 0.6, 0.2] }}
                                    transition={{ duration: 2, repeat: Infinity, ease: "easeInOut" }}
                                />
                            </div>
                            <div className="relative z-10 flex h-[360px] w-full items-center justify-center md:h-[420px]">
                                <div className="text-center">
                                    <div className="bg-gradient-to-b from-white/95 via-white/80 to-white/50 bg-clip-text text-transparent text-2xl font-semibold md:text-3xl">
                                        Signals meet judgment
                                    </div>
                                    <div className="mt-2 text-white/70">SOC teams connect threats to people</div>
                                </div>
                            </div>
                        </div>
                    </motion.div>

                    <motion.div
                        initial={{ opacity: 0, x: 16 }}
                        animate={seen ? { opacity: 1, x: 0 } : { opacity: 0, x: 16 }}
                        transition={{ duration: 0.6, ease: "easeOut", delay: 0.15 }}
                        className="max-w-xl flex-1 self-center"
                    >
                        <div className="inline-flex items-center rounded-full p-[1px] ring-1 ring-white/10">
                            <span className="flex items-center gap-2 rounded-full bg-[#0A0D12]/80 px-3 py-1 text-md font-medium text-white/90 ring-1 ring-white/10 backdrop-blur-sm">
                                Why SOC Teams Matter
                            </span>
                        </div>
                        <h2 className="mt-5 text-display-xs font-semibold text-white md:text-display-sm">
                            Why organizations rely on SOC teams
                        </h2>
                        <p className="mt-2 text-md text-white/75 md:mt-4 md:text-lg">
                            Security tools generate alerts, but it is SOC teams that interpret signals, investigate incidents, and
                            respond before threats escalate.
                        </p>
                        <ul className="mt-8 flex flex-col gap-4 pl-2 md:gap-5 md:pl-4">
                            {[
                                "Tools detect, humans decide",
                                "Context matters more than volume",
                                "Response timing determines impact",
                            ].map((point) => (
                                <li key={point} className="flex items-center gap-3">
                                    <TagCheckbox size="lg" isSelected />
                                    <span className="text-md text-white/80">{point}</span>
                                </li>
                            ))}
                        </ul>
                    </motion.div>
                </div>
            </div>
        </section>
    );
};
